    transform: translateY(4vw);
  }
  100% {
    transform: translateY(-4vw);
  }
}
.why-us {
  margin-bottom: 7.813vw;
}
.why-us .titles {
  margin-bottom: 0;
}
.why-us__title {
  text-align: center;
  font-weight: 500;
  font-size: 1.979vw;
  line-height: 2.292vw;
  position: relative;
  padding-top: 1vw;
  margin-bottom: 2.083vw;
}
.why-us__title::before {
  content: "";
  position: absolute;
  top: 0;
  left: 50%;
  transform: translateX(-50%);
  width: 6.458vw;
  height: 0.152vw;
  background-color: #d3d1d1;
}
.why-us__text {
  text-align: center;
  font-size: 1.042vw;
  line-height: 1.771vw;
  margin-bottom: 3.125vw;
}
.why-us__text p:not(:last-child) {
  margin-bottom: 1.563vw;
}
.why-us__images {
  display: grid;
  grid-template-columns: repeat(5, 1fr);
  grid-template-rows: repeat(2, 1fr);
  grid-column-gap: 1.25vw;
  grid-row-gap: 1.25vw;
  margin-bottom: 8.333vw;
}
.why-us__img {
  border-radius: 0.781vw;
  height: 100%;
  width: 100%;
}
.why-us__img:nth-child(1) {
  grid-area: 1/1/3/2;
}
.why-us__img:nth-child(2) {
  grid-area: 1/2/2/3;
}
.why-us__img:nth-child(3) {
  grid-area: 2/2/3/3;
}
.why-us__img:nth-child(4) {
  grid-area: 1/3/3/4;
}
.why-us__img:nth-child(5) {
  grid-area: 1/4/2/5;
}
.why-us__img:nth-child(6) {
  grid-area: 1/5/2/6;
}
.why-us__img:nth-child(7) {
  grid-area: 2/4/3/6;
}
.why-us__provide {
  background-color: var(--red-color);
  padding: 2.083vw 0vw;
  position: relative;
  overflow: hidden;
}
.why-us__provide-title {
  text-align: center;
  text-transform: uppercase;
  color: var(--light-color);
  font-family: var(--font-family-Amiko);
  font-weight: 700;
  font-size: 2.604vw;
  line-height: 3.438vw;
}
.why-us__boubles {
  position: absolute;
  top: 0vw;
  left: 13vw;
  width: 12.605vw;
  height: 15.313vw;
  opacity: 0.7;
}
.why-us__pattern {
  position: absolute;
  top: -1.7vw;
  right: 13vw;
  width: 11.824vw;
  height: 6.928vw;
  opacity: 0.7;
}

.our-results {
  margin-bottom: 8.333vw;
}
.our-results__content {
  display: flex;
  justify-content: space-between;
  margin-bottom: 3.125vw;
}
.our-results__text {
  text-align: center;
  text-indent: 1.1vw;
