}
.count-results__title {
  font-weight: 600;
  font-size: 1.458vw;
  line-height: 1.667vw;
}

.contacts {
  padding-bottom: 8.333vw;
  position: relative;
  overflow: hidden;
}
.contacts__bg {
  position: absolute;
  bottom: 0;
  left: 0;
  height: 65.313vw;
