  position: relative;
  width: 50%;
  overflow: hidden;
  border-top-right-radius: 0.7vw;
  border-bottom-right-radius: 0.7vw;
}
.contacts__map svg {
  position: absolute;
  top: 33%;
  left: 37%;
  fill: var(--red-color);
  width: 4.688vw;
  height: 3.646vw;
}
.contacts__map::after {
  content: "";
  position: absolute;
  top: 51%;
  left: 46%;
