}
.contacts .our-offices__item:first-child,
.contacts .our-offices__item:nth-child(4) {
  transform: translateY(0);
}
.contacts .our-offices__item {
  width: 34.948vw;
  height: auto;
  padding: 0;
}
.contacts .office-card {
  padding: 0;
  width: 100%;
  height: auto;
  flex-direction: inherit;
  box-shadow: 0vw 0.208vw 2.604vw rgba(0, 0, 0, 0.1);
  backdrop-filter: blur(0.26vw);
}
.contacts .office-card__info {
